.modal {
    &-background {
        z-index: 10;
        background-color: rgba(0, 0, 8, 0.25);
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }

    &-content {
        position: absolute;
        top: 40px;
        width: 560px;
        height: 560px;
        overflow-y: scroll;
        background-color: #fff;
        transform: translateX(-50%);
        left: 50%;
        padding: 20px;
    }
}

.modal-connection {
    text-align: center;

    &_title {
        font-size: 18px;
        font-weight: normal;
        padding-bottom: 15px;
    }

    &_description {
        font-size: 14px;
    }

    &_qr-code {
        width: 300px;
        height: 300px;
    }

    &-credentials {
        font-size: 18px;
    }
}